:root {
  --primary-link-color: #000;
  --second-link-color: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  margin: 0;
  padding: 0;
  display: block;
}

.nav {
  margin-left: auto;
}

.logo_img {
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
  display: block;
}

.nav_list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  margin-left: 20px;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav_link.is-active {
  color: #00acdc;
}

